.feedback {
  width: 100%;
  margin: 0;
  padding: 0;

  background-color: $color-color;
}

.feedback__button {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0;
}

.feedback__button button {
  width: 270px;
  min-height: 45px;

  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  font-family: $font-family-main;
  color: $light-color;

  background-color: $background-button-color;
  border: 1px solid $light-color;
  border-radius: 5px;

  cursor: pointer;
}

.feedback__checkbox {
  position: relative;

  width: 230px;
  margin: 0;
  padding-top: 8px;

  opacity: 0.8;
  cursor: pointer;
}

.feedback__checkbox label {
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  font-family: $font-family;
  color: $light-color;
}

.feedback__checkbox span {
  position: absolute;
  content: "";
  top: 9px;
  left: -33px;
  z-index: 3;

  width: 16px;
  height: 16px;
  margin: 0;
  padding: 0;
  padding-top: 4px;
  padding-left: 3px;

  border: 1px solid $light-color;
}

.feedback__checkbox input + label svg {
  display: none;
}

.feedback__checkbox input:checked + label svg {
  display: block;
}

.feedback__form {
  width: 570px;
  margin: 0;
}

.feedback__form li {
  list-style: none;
}

.feedback__form textarea {
  width: 100%;
  min-height: 154px;
  margin-bottom: 24px;

  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  font-family: $font-family;
  color: #000000;

  border: none;
}

.feedback__form ul {
  display: flex;
  justify-content: space-between;
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
}

.feedback h2 {
  width: 570px;
  margin: 0;
  margin-right: 5px;
  margin-bottom: 11px;
  padding: 0;

  font-size: 36px;
  font-weight: 400;
  line-height: 51px;
  font-family: $font-family-main;
  color: $light-color;
}

.feedback__list input {
  position: relative;

  width: 270px;
  min-height: 50px;

  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  font-family: $font-family;
  //color: $light-color;

  border: none;
}
