  position: absolute;
  content: "";
  top: 0;
  left: 0;
  z-index: 1;

  background-color: $light-color;
  opacity: 0.1;
}*/

.feedback__wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  max-width: 1920px;
  margin: 0 auto;
  padding-top: 90px;
  padding-right: 357px;
  padding-bottom: 115px;
}

.feedback__wrapper > p {
  width: 570px;
  margin: 0;
  margin-bottom: 50px;

  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  font-family: $font-family;
  color: $light-color;
}

@media (max-width: $tablet-width) {
  .feedback h2 {
    font-size: 32px;
    line-height: 45px;
  }
}

@media (max-width: $mobile-width) {
  .feedback h2 {
    font-size: 28px;
    line-height: 39px;
  }
}
